#intro{
    height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 75rem;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
}
.bg{
    position: absolute;
    top: -1rem;
    right: 0;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
}
.introContant{
    height: 100vh;
    width: 100vw;
    padding: 2rem;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.hello{
   font-size: 1.75rem;
   font-weight: 100;

}
.introName{
    color: yellow;
}
.introPara{
    font-size: medium;
    font-weight: 300;
    letter-spacing: 1px;
}
.btn{
    background: white;
    margin: 1rem 0;
    height: 2.8rem;
    width: 7.5rem;
    border: none;
    border-radius: 2rem;
    font-size: 15px;
}

@media screen and (max-width:840px) {
    
    .bg{
        position: absolute;
        top: -1rem;
        right: 0;
        z-index: -1;
        object-fit: cover;
        height: 100vh;
    }
    .introContant{
        font-size: 5vw;
    }
    .introText{
        font-size: 3rem;
    }
}

@media screen and (max-width:480px) {
    
    .bg{
       right: -20vw;
    }
    .introText{
      line-height: 3rem;
      font-size: 2rem;
    }
    .introContant{
        padding: 3rem;
    }
  
}